___CSS_LOADER_EXPORT___.push([module.id, `:root {
  color-scheme: dark;

  --background: #15130b;
  --surface: #222017;
  --surface-high: #2d2a21;
  --outline: #969080;
  --text: #e8e2d4;
  --text-secondary: #cdc6b4;
  --primary: #dbc66e;
  accent-color: var(--primary);
  --on-primary: #3a3000;

  --rounded-small: 1rem;
  --rounded-card: 1.5rem;
  --rounded-large: 9999px;
}

body {
  margin-top: 6rem;

  display: grid;
  justify-items: center;

  background-color: var(--background);
  color: var(--text);
}

main {
  min-width: 50%;
  max-width: 32rem;

  display: grid;
  gap: 3rem;
}

.projects {
  display: flex;
  gap: 1rem;
}
.projects-list {
  display: flex;
  gap: 1rem;
  flex: 1;
}
.projects li {
  flex: 1;
  text-align: center;
}
.projects label {
  display: block;

  padding: 0.75rem 3rem;

  background-color: var(--surface);
  border-radius: var(--rounded-small);
}
.projects :checked ~ label {
  background-color: var(--primary);
  color: var(--on-primary);
}

.todos-list {
  display: grid;
  gap: 1rem;
}
.todos-list li {
  padding: 2rem 3rem;

  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 2rem;
  align-items: center;

  background-color: var(--surface);
  border-radius: var(--rounded-card);
}
.todos-list input[type="checkbox"] {
  width: 2rem;
  height: 100%;
}
.todos-list .priority-1 {
  background-color: var(--surface-high);
}
.todos-list .actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  align-items: center;
}

.add-todo {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-flow: column;
  align-items: end;
  gap: 0.5rem;
}
.add-todo button {
  /* input's height so that it looks better */
  height: 3.5rem;
}

.styled-input {
  padding: 0.5rem 1rem;
  border: 1px solid var(--outline);

  background-color: var(--surface);
  color: var(--text-secondary);
  border-radius: var(--rounded-small);
  outline: none;

  font-size: 1.5rem;
}
.styled-input:focus {
  border-color: var(--primary);
}

.form-column {
  display: grid;
  gap: 0.25rem;
}

.styled-button {
  height: fit-content;
  padding: 0.75rem 4rem;
  border: none;

  background-color: var(--primary);
  color: var(--on-primary);
  border-radius: var(--rounded-large);

  font-size: 0.875rem;
  font-weight: 500;
}

.icon-button {
  height: 3rem;
  width: 3rem;
  border: 1px solid var(--outline);

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: var(--surface);
  color: var(--text);
  border-radius: var(--rounded-large);
}

dialog[open] {
  border: none;
  padding: 2rem;
  margin: auto;

  display: grid;

  border-radius: var(--rounded-card);
}

dialog form {
  display: grid;
  gap: 0.5rem;
}

dialog .dialog-actions {
  margin-top: 1rem;

  display: flex;
  gap: 0.5rem;
}
