*::before,
*::after {
  box-sizing: border-box;
}
body {
  scroll-behavior: smooth;
  line-height: 1.5;
}
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}
input,
button,
textarea,
select {
  font: inherit;
}
button {
  transition: filter 150ms, transform 100ms;
}
button:hover {
  filter: brightness(1.25);
}
button:active {
  transform: scale(0.95);
}

/* typography */
